import './CreateAccount.css';
import {motion} from "framer-motion";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useState,useContext } from "react";
import Axios from "axios";
import {ConnectConfig} from '../App.js';
import cookie from "js-cookie";

export const CreateAccount=()=>{
    const [success,setSuccess] = useState(true);
    const {IpAddress}=useContext(ConnectConfig);
    const schema = yup.object().shape({
        userName: yup.string("").required(""),
        email: yup.string().email("").required(""),
        password: yup.string().required("").matches(/.*[A-Za-z]+.*/,""),
        confirmPassword: yup
            .string("")
            .oneOf([yup.ref("password"),null],"")
            .required(""),
        school: yup.string("").required("")
    });
    const {register,handleSubmit,reset,formState:{errors}} = useForm({
        resolver: yupResolver(schema),
    });
    if(!cookie.get('userLogin')) return;

    const onSt = (data,e)=>{
        let info = {
            userName:data.userName,
            email:data.email,
            password:data.password,
            school:data.school
        };
        addAccount(info,e); 
    }
    
    const addAccount = (info,element)=>{
        
        Axios.post(`https://${IpAddress}/createAccount`,info)
        .then((response)=>{ 
            if(response.data==='thereIsData'){
                alert("!");
                return;
            }
            Axios.post(`https://${IpAddress}/createClass`,{
                teacherId:response.data[0]._id,
            })
        }).then(()=>{
            setSuccess(true);
            alert("")
            reset();
        })
    }

    return (
        <div className="insertContainer">
            <motion.form
            exit = {{scale:[1,1.2,0]}}
            animate={{scale:[0,1.2,1]}} initial={{scale:0}}
            className="insertNail"
            onSubmit={handleSubmit(onSt)}>
                <table>
                    <tbody>
                    <tr><td id="nailTitle"></td></tr>
                    <tr><td className="nailContent"> :</td></tr>
                    <tr>
                        <td>
                        <input
                        className="nailInput"
                        {...register("userName")}
                        placeholder="..."/>
                        </td>
                    </tr>
                    <tr><td className="nailError">{errors.userName?.message}</td></tr>
                    <tr><td className="nailContent"> :</td></tr>
                    <tr>
                        <td>
                        <input
                        className="nailInput"
                        {...register("email")}
                        placeholder="..."/>
                        </td>
                    </tr>
                    <tr><td className="nailError">{errors.email?.message}</td></tr>
                    
                    <tr><td className="nailContent"> :</td></tr>
                    <tr>
                        <td>
                        <input
                        className="nailInput"
                        type = "password"
                        {...register("password")}
                        placeholder="..."/>
                        </td>
                    </tr>
                    <tr><td className="nailError">{errors.password?.message}</td></tr>

                    <tr><td className="nailContent"> :</td></tr>
                    <tr>
                        <td>
                        <input
                        className="nailInput"
                        type = "password"
                        {...register("confirmPassword")}
                        placeholder="..."/>
                        </td>
                    </tr>
                    <tr><td className="nailError">{errors.confirmPassword?.message}</td></tr>

                    <tr><td className="nailContent"> :</td></tr>
                    <tr>
                        <td>
                        <input
                        className="nailInput"
                        {...register("school")}
                        placeholder="..."/>
                        </td>
                    </tr>
                    <tr><td className="nailError">{errors.school?.message}</td></tr>
                    </tbody>
                </table>
                <motion.button
                id="nailButton"
                whileHover={{scale:1.1}}
                whileTap={{scale:0.9}}
                >
                
                </motion.button>
            </motion.form>
            
        </div>
    );
};